.sendForm {
    display: flex;
    align-items: center;
    justify-content: space-around;
    margin: 56px 0 32px 0;
}

.textarea {
    width: 976px;
    background-color: #F5F7FB;
    border-radius: 25px;
    overflow: hidden;
    resize: none;
    font-size: 14px;
    padding: 12px 24px;
}

.button {
    padding: 10px 40px;
    border: none;
    color: #FFFFFF;
    background-color: #0066CC;
    font-size: 16px;
    font-weight: 600;
    border-radius: 20px;
    transition: background-color 0.6s linear;
}

.button:hover {
    cursor: pointer;
    background-color: yellow;
    color: #0059b3;
}

